<template>
  <div class="y-describe" :style="{ width: describeWidth }">
    <input id="y-describe-input" v-model="check" class="y-describe-input" type="checkbox" />
    <div class="y-describe-text">
      <y-text-link class="y-describe-btn" @click="checkClick" type="light">{{ check ? foldText : unFoldText }}</y-text-link>
      <span class="y-describe-content"><slot /></span>
    </div>
  </div>
</template>
<script>
export default {
  name: 'YDescribe',
  props: {
    foldText: {
      type: String,
      default: function() {
        return this.$wci18n.t('wh.describe.foldText');
      },
    },
    unFoldText: {
      type: String,
      default: function() {
        return this.$wci18n.t('wh.describe.unFoldText');
      },
    },
    width: {
      type: [String, Number],
      default: '100%',
    },
  },
  data() {
    return {
      check: false,
      describeWidth: this.width,
    };
  },
  methods: {
    checkClick() {
      this.check = !this.check;
      this.describeWidth = this.check ? '100%' : this.width;
      this.$emit('click', this.check);
    },
  },
};
</script>
<style lang="less">
.y-describe {
  display: flex; //  text before 
  overflow: hidden;
  .y-describe-text {
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: justify;
    position: relative;
    line-height: 1.5;
    max-height: 1.5em;
    transition: 0.3s max-height;
    &::before {
      content: '';
      height: calc(100% - 21px); // text btn btn
      float: right; // btn
    }
    &::after {
      //  btn , btn  btn 
      content: '';
      width: 999vw;
      height: 999vw;
      position: absolute;
      box-shadow: inset calc(100px - 999vw) calc(30px - 999vw) 0 0 #fff;
      margin-left: -100px;
    }
    .y-describe-content {
      // 
      word-break: break-all; // 
    }
    .y-describe-btn {
      // 
      position: relative;
      float: right;
      clear: both;
      margin-left: 20px;
      cursor: pointer;
      color: #3158cc;
      &::before {
        // 
        content: '...';
        position: absolute;
        left: -5px;
        color: #333;
        transform: translateX(-100%);
      }
    }
  }
  .y-describe-input {
    display: none;
  }
  // 
  .y-describe-input:checked + .y-describe-text {
    max-height: none; // 
  }
  .y-describe-input:checked + .y-describe-text::after {
    visibility: hidden; //
  }

  .y-describe-input:checked + .y-describe-text .y-describe-btn::before {
    // 
    visibility: hidden;
  }
}
</style>
